import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { Icon } from '../atoms/Icon'
import { useClosingSideMenu } from '../../hooks/closeSideBar'
import './asside.scss'

export const Asside = () => {
  const [isOpen, setIsOpen] = useState(false)

  // useEffect(() => {
  // }, [])
  useClosingSideMenu(setIsOpen)
  
  const hedlerCloseAside = () => {
    setIsOpen((old) => !old)
  }

  const data = [
    { id: 1, title: '', img: 'kitchen' },
    { id: 2, title: '', img: 'bedroom' },
    { id: 3, title: '', img: 'livingroom' },
    { id: 4, title: '', img: 'closet' },
    { id: 5, title: ' ', img: 'office' },
    { id: 6, title: '', img: 'childrensroom' },
    { id: 7, title: '', img: 'actions' },
    { id: 8, title: '', img: 'new' },
    { id: 9, title: '', img: 'mattress' },
    { id: 10, title: '', img: 'armchair' },
    { id: 11, title: '', img: 'cupboard' },
  ]

  return (
    <div className={!isOpen ? 'asside' : ['asside active']}>
      {data.map((asside) => {
        return (
          <Link to="/" className="asside__item" key={asside.id}>
            <Icon iconId={asside.img} width="22" height="22" />
            <span>{asside.title}</span>
          </Link>
        )
      })}
      <button onClick={hedlerCloseAside} className="asside__btn-close"></button>
    </div>
  )
}
