import './product.scss'
import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { Icon } from '../atoms/Icon'

export const Product = ({ card }) => {

  const [like, setLike] = useState(false)
  const { title, id, description: subtitle, price, image } = card

  const toggleLike = (e) => {
    e.preventDefault()
    setLike(!like)
  }

  const changeProduct = (e) => {
    if (e.target.tagName === 'svg' || e.target.tagName === 'BUTTON') {
      e.stopPropagation()
      e.preventDefault()
    }
  }

  return (
    <Link
      onClick={(e) => changeProduct(e)}
      to={`products/${id}`}
      className="product"
    >
      <div className="product__wrap">
        <button
          onClick={(e)=> toggleLike(e)}
          className={[like ? 'product__like active' : 'product__like']}
        >
          <Icon iconId="like" width="19" height="17" />
        </button>
        <div className="product__image">
          <img src={image} alt="c" />
        </div>
        <div className="product__all">
          <div className="product__title">{title}</div>
          <div className="product__subtitle">{subtitle}</div>
          <div className="product__price">{price}</div>

          <div className="product__drop-info">
            <div className="product__size"></div>
            <div className="product__info">
              <div className="product__info-item">
                <h3></h3>
                <p>43 </p>
              </div>
              <div className="product__x"></div>
              <div className="product__info-item">
                <h3></h3>
                <p>43 </p>
              </div>
              <div className="product__x"></div>
              <div className="product__info-item">
                <h3></h3>
                <p>77 </p>
              </div>
            </div>
            <button className="product__btn">  </button>
          </div>
        </div>
      </div>
    </Link>
  )
}
